:root {
  --main-black: rgba(39, 39, 39, 1);
  --main-grey: rgba(24, 24, 24, 1);
  --main-light: rgba(255, 255, 255, 1);
  --main-yellow: rgba(244, 206, 12, 1);
  --bright-yellow: rgba(255, 199, 0, 1);
  --btn-black: rgba(13, 12, 17, 1);
  --btn-no_active: rgba(218, 218, 218, 1);
  --text_btn-no_active: rgba(142, 142, 142, 1);
  --main-green: rgba(59, 222, 124, 1);
}

a {
  color: inherit;
  text-decoration: none;
}

h1,
h2,
h3,
h4,
p {
  margin: 0;
  text-decoration: none;
}

ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

button {
  margin: 0;
  padding: 0;
  font-family: inherit;
  background-color: var(--white-color);
  cursor: pointer;
  box-shadow: none;
  border: none;
}

ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

button {
  margin: 0;
  padding: 0;
  font-family: inherit;
  background-color: transparent;
  cursor: pointer;
  box-shadow: none;
  border: none;
}

body {
  font-family: 'PT Serif', sans-serif;
  color: var(--main-black);
  min-height: 100vh;
  display: flex;
  flex-direction: column;

  // &.no-scroll {
  //   overflow: hidden;
